.button {
  font-size: 16px;
  font-weight: bold;
  border: none;
  background-image: linear-gradient(rgb(132, 0, 0), red);
  color: white;
  border-radius: 5px;
  padding: 5px 20px;
  padding-top: 7px;
  transition: all 0.1s;
  cursor: pointer;
  &:hover {
    transform: scale(1.05);
  }
}
